import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { LocalDataSource } from 'ng2-smart-table';
import { RepositoryService } from '../../../@core/data/inventorydata/services/repository.service';
import { User, UserObject } from '../../../@core/data/inventorydata/models/user';

@Component({
  selector: 'ngx-users-list',
  templateUrl: './users-list.component.html',
  styleUrls: ['./users-list.component.scss']
})
export class UsersListComponent implements OnInit {

  settings = {
    pager: {
      display: true,
      perPage: 25
    },
    add: {
      addButtonContent: '<i class="nb-plus"></i>',
      createButtonContent: '<i class="nb-checkmark"></i>',
      cancelButtonContent: '<i class="nb-close"></i>',
      show: false,
    },
    edit: {
      editButtonContent: '<i class="nb-edit"></i>',
      saveButtonContent: '<i class="nb-checkmark"></i>',
      cancelButtonContent: '<i class="nb-close"></i>',
    },
    delete: {
      deleteButtonContent: '<i class="nb-trash"></i>',
      confirmDelete: true,
    },
    columns: {
      id: {
        title: 'ID',
        type: 'number',
        filter: false,
      },
      fullname: {
        title: 'Emp. Name',
        type: 'string',
        filter: false,
      },
      username: {
        title: 'Username',
        type: 'string',
        filter: false,
      },
      role: {
        title: 'Role',
        type: 'string',
        filter: false,
      },
      isActive: {
        title: 'Active',
        type: 'boolean',
        filter: false,
      },
    },
  };

  source: LocalDataSource = new LocalDataSource();
  users: User[] = new Array<UserObject>();
  isLoadingResults = true;

  @Output() departmentEvent: EventEmitter<User> = new EventEmitter<User>();

  constructor(private repository: RepositoryService) { }

  ngOnInit() {
    this.loadData();
  }

  loadData() {
    let apiRoute: string = 'users/';
    this.repository.getData(apiRoute)
      .subscribe(results => {
        this.users = results as User[];
        this.source.load(this.users);
        this.isLoadingResults = false;
      },
        error => {
          alert(error);
          console.log(error);
        }
      )
  }

  onRowSelect(event): void {
    this.departmentEvent.emit(event.data);
  }

  onDeleteConfirm(event): void {
    if (window.confirm('Are you sure you want to delete?')) {
      event.confirm.resolve();
    } else {
      event.confirm.reject();
    }
  }

  onSearch(query: string = '') {
    if (query == '') {
      this.source.setFilter([], true, false);
      this.source.refresh();
    }
    else
      this.source.setFilter([
        // fields we want to include in the search
        {
          field: 'name',
          search: query
        },
        {
          field: 'quotaGroupName',
          search: query
        },
        {
          field: 'email',
          search: query
        }
      ], false);
    // second parameter specifying whether to perform 'AND' or 'OR' search 
    // (meaning all columns should contain search query or at least one)
    // 'AND' by default, so changing to 'OR' by setting false here
  }

}
